<template>
  <div class="home">
    <div class="top">
      <div @click='goTo()' class="go">{{ $t("message.f5_32") }}</div>
      <div class="lang">
        <span @click="changeLanguage('zhCHS')">中文</span> /
        <span @click="changeLanguage('en')">EN</span>
      </div>
      <img src="../../assets/img/logo.png" alt="" width="50%" />
      <div style="text-align: center">
        <p>{{ $t("message.f5_1") }}</p>
      </div>
      <div class="turnover" v-if="dogesInfo">
        <h1 >{{dogesInfo?dogesInfo.info[1]:0 | figure}} <span>BNB</span></h1>
        <!-- <h1 v-else>{{ $t("message.f5_17") }}</h1> -->
        <h4>{{ $t("message.f5_2") }}</h4>
      </div>
    </div>
    <div class="line"></div>
    <div class="time_box">
      <div class="time">
        <span v-if="dogesInfo&&getTimeFun(dogesInfo.info[4])>0">{{ $t("message.f5_3") }}</span>
        <span v-else>{{ $t("message.f5_27") }}</span>
        <div v-if="dogesInfo&&dogesInfo.info[0]==0">
          <van-count-down  v-if="getTimeFun(dogesInfo.info[4])>0" millisecond  :time="dogesInfo?getTimeFun(dogesInfo.info[4]):0" format="HH:mm:ss:SS"/>
            <h1 v-else> {{dogesInfo?(dogesInfo.info[1]-dogesInfo.info[2]) : 0 | figure10}}</h1>
        </div>
        <h1 v-else>{{ $t("message.f5_17") }}</h1>
        <div class="progress-box">
          <div class="progress">
            <div class="jinfu" v-if="dogesInfo" :style="{ width: dogesInfo.progressWidth + '%' }"></div>
          </div>
          <p>{{ $t("message.f5_4") }}<span>{{dogesInfo?dogesInfo.info[2]:0 | figure}}/{{dogesInfo?dogesInfo.info[1]:0 | figure}}</span></p>
        </div>
      </div>
    </div>
    <div class="line"></div>

    <div class="change" v-if="dogesInfo&&dogesInfo.info[0]==0">
      <div class="title">
        {{ $t("message.f5_5") }}
        <p><span> 1 BNB={{dogesInfo? (1000000000000000000 / dogesInfo.info[7] *dogesInfo.info[6]):0 | figureFive}} FIVE</span></p>
        <!-- {{ $t("message.f5_6") }} -->
      </div>
      <div class="coin">
        <div class="coin_top">
          <!-- <div class="coin_img"> <img src="../../assets/img/coin.png" alt="" width="35" /></div> -->
          <img src="../../assets/img/bnb.png" alt="" width="35" />
          <span style="color:#f3c52a">BNB：</span>
          {{Balance | price10}}
        </div>
        <input type="number" v-model="money" placeholder="0" @input="inputEnter" :disabled="input_cols" />
          <div class="tips">{{ $t("message.f5_25") }}{{dogesInfo? 500000000000000000-(dogesInfo.info[8]):0 | figure10}}NBN。{{ $t("message.f5_26") }}</div>
      </div>
      <img src="../../assets/img/change.png" alt="" class="change_icon" />
      <div class="coin">
        <div class="coin_top">
          <!-- <div class="coin_img"> <img src="../../assets/img/coin.png" alt="" width="35" /></div> -->
          <img src="../../assets/img/coin.png" alt="" width="35" />
          <span>FIVE：</span>
          {{FIVEBalance | figureFive}}
        </div>
        <div class="input_l">{{dogesInfo? getFiveNum(this.money):0 | figureFive}}</div>
        <!-- <input type="number" disabled v-model="1"/> -->
      </div>
      <div class="tips">{{ $t("message.f5_19") }}0.1</div>
     
      <!-- <button class="onBtn">{{ $t("message.f5_7") }}</button> -->
      <button class="onBtn" v-if="dogesInfo&&getTimeFun(dogesInfo.info[4])>0">{{ $t("message.f5_30") }}</button>
      <button class="Btn" v-else @click="investIn">{{ $t("message.f5_18") }}</button>
      <img src="../../assets/img/five.png" alt="" class="five_img" />
    </div>

    <div class="change">
      <div class="title">{{ $t("message.f5_8") }}</div>
      <div class="info">
        <van-row>
          <van-col span="24">
            <div class="">
              <span>{{ $t("message.f5_9") }}</span>
              <h1>{{dogesInfo?dogesInfo.info[8]:0 | figure10}} BNB</h1>
              <!-- <h1>{{dogesInfo?getFIVE(dogesInfo.info[8], dogesInfo.info[6]):0 }} <small>FIVE</small>  <span style="font-size:13px;font-weight: normal; ">≈ {{dogesInfo?dogesInfo.info[8]:0 | figure10}} BNB</span>  </h1> -->
            </div>
          </van-col>
        </van-row>
        
        <van-row>
          <van-col span="12">
            <div class="">
              <span>{{ $t("message.f5_10") }}</span>
              <h2>{{dogesInfo?(Number(dogesInfo.info[9]) + Number(dogesInfo.info[12])):0 | figureFive}}</h2>
            </div>
          </van-col>
          <van-col span="12" v-if="dogesInfo&&dogesInfo.info[12]==0">
            <div class="">
              <span>{{ $t("message.f5_11") }}</span>
              <h3 >{{dogesInfo?dogesInfo.info[10]:0 | figureFive}}</h3>
            </div>
          </van-col>
           <van-col span="12" v-else>
            <div class="">
              <span>{{ $t("message.f5_29") }}</span>
               <h3>{{dogesInfo?dogesInfo.info[12]:0 | figureFive}}</h3>
            </div>
          </van-col>
        </van-row>
      </div>
      <div v-if="dogesInfo && dogesInfo.info[12]>0">
         <!-- <div v-if="dogesInfo && dogesInfo.info[13]==0"> -->
            <button class="Btn" v-if="dogesInfo && dogesInfo.info[13]==1" @click="withdraw">{{ $t("message.f5_12") }}</button>
            <button class="onBtn" v-else>{{ $t("message.f5_28") }}</button>
          <!-- </div> -->
          <!-- <div v-else>  <button class="onBtn">{{ $t("message.f5_24") }}</button></div> -->
      </div>
      <div v-else-if="dogesInfo && dogesInfo.info[8]<=0"> 
        <button class="onBtn">{{ $t("message.f5_31") }}</button>
      </div>    
      <div v-else>
           <div v-if="dogesInfo && dogesInfo.info[10]>0">
            <button class="Btn" v-if="dogesInfo && getTimeFun(dogesInfo.info[11])<=0" @click="withdraw">{{ $t("message.f5_12") }}</button>
            <button class="onBtn" v-else><van-count-down :time="dogesInfo?getTimeFun(dogesInfo.info[11]):0"  /></button>
          </div>
          <div v-else>  <button class="onBtn">{{ $t("message.f5_24") }}</button></div>
      </div>
     
      
      <img src="../../assets/img/five.png" alt="" class="five_img" />
    </div>

    <div class="foot">
      <div class="foot_a">
        <van-row gutter="20" justify="space-around">
          <van-col span="12"
            ><a href="http://ffive.finance/FIVEWhitePaper.pdf">
              {{ $t("message.f5_13") }}</a
            ></van-col
          >
          <van-col span="12"
            ><a href="https://f5five.github.io">
              {{ $t("message.f5_14") }}</a
            ></van-col
          >
          <!-- <van-col span="8"
            ><a href=""> {{ $t("message.f5_15") }}</a></van-col
          > -->
        </van-row>
      </div>
      <div class="icon">
        <a href="https://twitter.com/F5five_officials"
          ><img src="../../assets/img/foot_1.png" alt="" width="30"
        /></a>
        <a href="https://t.me/f5_dao"
          ><img src="../../assets/img/foot_2.png" alt="" width="30"
        /></a>
        <!-- <a href=""
          ><img src="../../assets/img/foot_3.png" alt="" width="30"
        /></a>
        <a href=""
          ><img src="../../assets/img/foot_4.png" alt="" width="30"
        /></a> -->
      </div>
    </div>
  </div>
</template>

<script type="es6">
import { mapActions, mapState } from "vuex";
import Web3 from "web3";
export default {
  data() {
    return {
      time: 43984932483,
      money:"",
      input_cols:false,
      timer:null,
      timer2:null,
      // progressWidth: 30,
    };
  },
  computed: {
    ...mapState("main", [
      "dogesInfo",
      "Balance",
      "FIVEBalance",
      "account",
      // "balance",
      "poolContract",
      // "mxtContract",
      // "hmdBalance",
    ]),
  },
  methods: {
    ...mapActions("main", [
      // "add_price",
      "add_DOGESpool",
      "add_dogCoinContract",
      // "add_hmdCoinContract",
    ]),
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      localStorage.setItem("lang", lang);
      // this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
     goTo(){
 //直接跳转
 this.$router.push('/');
 },
    getFiveNum(num){
       var _this =this;
      if (_this.dogesInfo == null) {
        _this.$toast(_this.$t("message.noMask"));
        return;
      }
      if(num){
        console.log()
       return (num*10**18) / this.dogesInfo.info[7]*this.dogesInfo.info[6];
        
      }else{
        return 0;
      }
    },
    getFIVE(num1,num2){
      var num = web3.utils.fromWei(num1, 'ether');
       var a = web3.utils.fromWei(num2, 'Gwei');
      return Number(num*a)
     
    },
    inputEnter(){
      var _this =this;
      if (_this.dogesInfo == null) {
        _this.$toast(_this.$t("message.noMask"));
        return;
      }
      var c = _this.money .toString();
      var a =c.split('.')[1];
      var b = (web3.utils.fromWei(_this.dogesInfo.info[7], 'ether')).split('.')[1];
      if(a&&a.length>1){
        // this.input_cols=true;
        // if(!/^\d+(\.\d{1,6})?$/.test(_this.money))
        var len = c.split('.')[0].length+1
        _this.money=Number(c.substring(0,len+1));
        console.log(len)
         _this.$toast(this.$t("message.f5_20"));
           return
      }
      
    },
    getTimeFun(time){
      if(time>0){
   var nowtime = new Date();
    var lefttime = time*1000 - nowtime.getTime();//距离结束时间的毫秒数
    return lefttime;
      }else{
         return 0;
      }
    
    },
       withdraw: function () {
      if (this.dogesInfo == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
    //  if(this.dogesInfo.info[10]=0){
    //    this.$toast(_this.$t("message.f5_21"));
    //    return
    //  }
      this.$toast.loading({
        message: 'loading',
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.poolContract.methods
        .withdraw()
        .send({ from: account })
        .then((res) => {
          console.log("withdraw", res);
            _this.$toast.clear();
         _this.$toast.success(_this.$t("message.f5_16"));
            _this.add_DOGESpool();
            _this.add_dogCoinContract();
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    investIn(){
      var _this=this;
      var account =this.account;
      if (this.dogesInfo == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      if(!this.money){
        this.$toast(_this.$t("message.f5_22"));
        return;
      }
      if(this.money>(0.5-Web3.utils.fromWei(_this.dogesInfo.info[8], 'ether'))){
        this.$toast(_this.$t("message.f5_23"));
        return;
      }
      console.log((_this.money*10**18) / this.dogesInfo.info[7],'2222222222222222222')
         this.$toast.loading({
        message: 'loading',
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(_this.money*10**18)
      // return
          this.poolContract.methods
        .investIn()
        .send({ from: account ,value:_this.money*10**18})
        .then((res) => {
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.f5_16"));
            _this.add_DOGESpool();
            _this.add_dogCoinContract();
          // _this.add_lpCoinContract();

          _this.money = 0;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });

    }
  },
  mounted() {
    var lang = localStorage.getItem("lang");
    this.changeLanguage(lang || "en");


    var getData = () => {
        this.add_DOGESpool();
    this.add_dogCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.dogesInfo || !this.Balance || !this.FIVEBalance || !this.poolContract) {
          getData();
        } else {
          // this.getCoinBalance();
          // this.isApproveFun();
          clearInterval(this.timer);
        }
      }, 1000);
    };
    getDataCalculate();

    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 30000);
  },
    destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.line {
  height: 7px;
  background: #232323;
  width: 100%;
}
.home {
  color: #fff;
      .go{
          position: absolute;
    right: 110px;
    top: 0;
    padding: 6px 10px;
    border: 1px solid #ffb520;
    border-radius: 5px;
    margin: 18px 15px 0 0;
    color: #ffa420;
    }
  .top {
    background: url(../../assets/img/bg_2.png) no-repeat top,
      url(../../assets/img/bf_3.png) no-repeat right bottom #171717;
    background-size: 100%, 100%;
    text-align: center;
    position: relative;
    padding: 95px 0 0 0;
    .lang {
      position: absolute;
      background: url(../../assets/img/lang_bg.png) no-repeat top;
      background-size: 100%;
      padding: 9px 20px;
      margin: 15px 15px 0 0;
      right: 0;
      top: 0;
      img {
        vertical-align: middle;
        margin-right: 5px;
      }
    }
    p {
      font-family: PingFang;
      font-weight: bold;
      color: #ffffff;
      font-size: 13px;
      position: relative;
      margin: 30px 0 20px;
      display: inline-block;
    }
    p::before {
      content: "";
      display: block;
      width: 10px;
      height: 10px;
      background: #ff8e21;
      position: absolute;
      left: -10px;
      top: -10px;
    }
    p::after {
      content: "";
      display: block;
      width: 10px;
      height: 10px;
      background: #282828;
      position: absolute;
      right: -10px;
      bottom: -10px;
    }
    .turnover {
      background: url(../../assets/img/left.png) no-repeat left,
        url(../../assets/img/right.png) no-repeat right;
      background-size: 40%, 40%;
      padding: 10px 0 40px;
      margin-top: 30px;
      h1 {
        font-size: 32px;
        font-family: Impact;
        font-weight: 400;
        color: #fd8d20;
        margin: 10px 0;
      }
    }
  }
  .time_box {
    background: url(../../assets/img/bg_1.png) repeat top;
    background-size: 100%;
    width: 94%;
    margin: 0 auto;
    padding: 30px 0;
    .time {
      background: url(../../assets/img/time.png) no-repeat top;
      background-size: 100%;
      text-align: center;
      padding: 18px 0;
      span {
        font-family: PingFang;
        font-weight: bold;
        color: #fa770e;
        margin-bottom: 25px;
        display: inline-block;
      }
      .progress-box {
        margin-top: 15px;
        .progress {
          background: #131313;
          border-radius: 7px;
          height: 9px;
          width: 85%;
          margin: 0 auto;
          .jinfu {
            height: 9px;
            background: #fd8d20;
            border-radius: 7px;
          }
        }
        p {
          width: 85%;
          margin: 8px auto;
          text-align: left;
          span {
            float: right;
          }
        }
      }
    }
  }
  .change {
    width: 92%;
    margin: 30px auto;
    box-sizing: border-box;
    background: url(../../assets/img/bg4.png) no-repeat top center #282828;
    background-size: 100%;
    border-radius: 10px;
    padding: 20px 20px;
    position: relative;
    .title {
      font-size: 20px;
      font-family: Microsoft YaHei;
      font-weight: bold;
      color: #ffffff;
      p {
        float: right;
        font-weight: normal;
        font-size: 14px;
        span {
          color: #ff8e21;
        }
      }
    }
    .title::after {
      content: "";
      display: block;
      width: 50px;
      height: 8px;
      background: #ff8e21;
      position: absolute;
      top: 0;
      // bottom: -10px;
    }
    .coin_top {
      margin: 20px 0 10px;
      display: flex;
      align-items: center;
      img {
        vertical-align: middle;
        margin-right: 6px;
      }
      span {
        font-size: 16px;
        font-weight: bold;
        font-family: FZCCHK;
        font-weight: normal;
        color: #ff8e21;
      }
    }
    input,.input_l {
      background: #171717;
      border-radius: 45px;
      border: none;
      width: 100%;
      font-size: 24px;
      padding: 12px 20px;
      box-sizing: border-box;
    }
    .change_icon {
      width: 20px;
      margin: 20px auto 0;
      display: block;
    }
    .onBtn {
      display: block;
      width: 65%;
      margin: 40px auto 40px;
      background: linear-gradient(0deg, #85a4a2 0%, #a2b2b4 100%);
      border-radius: 45px;
      border: none;
      padding: 14px 20px;
      font-size: 18px;
      font-family: Microsoft YaHei;
      font-weight: bold;
      color: #4e605f;
      .van-count-down{
        line-height: 26px;
        color: #4e605f;
      }
    }
    .five_img {
      width: 100px;
      display: block;
      margin: 0 auto 5px;
    }
    .info {
      margin: 30px 0;
      background: #171717;
      border-radius: 10px;
      padding: 20px 15px;
      h1 {
        font-size: 30px;
        font-family: Impact;
        font-weight: 400;
        color: #fd8d20;
        padding: 8px 0 15px;
        border-bottom: 1px solid #282828;
        margin-bottom: 20px;
      }
      h2 {
        font-size: 24px;
        font-family: Impact;
        font-weight: 400;
        color: #29f285;
        padding: 6px 0 0;
      }
      h3 {
        font-size: 24px;
        font-family: Impact;
        font-weight: 400;
        color: #09a0ff;
        padding: 6px 0 0;
      }
    }
    .Btn {
      display: block;
      width: 65%;
      margin: 30px auto 30px;
      background: linear-gradient(0deg, #ff8e21 0%, #ffcd1e 100%);
      border-radius: 45px;
      padding: 13px 20px;
      font-size: 18px;
      font-family: Microsoft YaHei;
      font-weight: bold;
      border: none;
      color: #933e01;
    }
  }
  .change::after {
    content: "";
    display: block;
    width: 110px;
    height: 48px;
    background: url(../../assets/img/bottom_bg.png) no-repeat top center;
    background-size: 100%;
    position: absolute;
    bottom: -16px;

    margin-left: -55px;
    left: 50%;
  }
  .foot {
    // margin-top: 80px;
    text-align: center;
    width: 60%;
    padding: 10px 0;
    margin: 60px auto 0;
    a {
      color: #fff;
      display: inline-block;
    }
    .foot_a {
      margin-bottom: 20px;
    }
  }
}
.tips{
  margin: 10px 0;
    color: #a9a8a6;
}
@media only screen and (max-width: 391px) {
  .van-count-down {
    font-size: 24px;
  }
  .home .time_box .time span {
    margin-bottom: 12px;
  }
  .info span {
    font-size: 12px;
  }
  .home .change .info h3,
  .home .change .info h2 {
    font-size: 20px;
    padding: 2px 0 0;
  }
  .home .top .turnover {
    margin-top: 18px;
  }
}
</style>



