<script type="es6">
import { mapActions, mapState } from "vuex";
import Web3 from "web3";
export default {
  data() {
    return {
      instNum:null,
       ArrList:null,
      transferList:null,
       allNum:null,
       newArr:[],
      input_cols:false,
      isApprove:false,
      isApprove2:false,
      timer:null,
      timer2:null,
      // progressWidth: 30,
    };
  },
  computed: {
    ...mapState("main", [
      "invitationContract",
      "transferContract",
      "FIVEBalance",
      "dogCoinContract",
       "account",
    ]),
  },
  methods: {
    ...mapActions("main", [
      "add_invitationContract",
       "add_dogCoinContract",
        "add_transferContract",
    ]),
         goTo(){
 //直接跳转
 this.$router.push('/index');
 },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      localStorage.setItem("lang", lang);
      // this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
    tips(){
      this.$toast(this.$t("message.f5_42"));
    },
trimSpace(array){  
    for(var i = 0 ;i<array.length;i++)  
    {  
       //这里为过滤的值
        if(array[i] == " " || array[i] == null || typeof(array[i]) == "undefined" || array[i] == '  ' )  
        {  
            array.splice(i,1);
            i= i-1;
        }  
    }  
     console.log(array);
    return array;  
},
    inputEnter(){
      var array=[];
       if(this.ArrList){
        array = this.ArrList.split(';');
        this.allNum = array.length*this.instNum
       }
   
    },
        // 授权
    lpApprove: function () {
      if (this.dogCoinContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      // var address = this.lpInfo.address;
      this.$toast.loading({
        message: "loading",
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      var account = this.account;
      this.dogCoinContract.methods
        .approve('0x0fe8400a0923deaa76c81f9188830ab853ff334b', "5000000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("授权=====", res);
          if (res.status) {
            this.$toast.success('OK');
            this.isApprove = true;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    // 是否授权
    isApproveFun: function () {
      if (this.dogCoinContract) {
        var account = this.account;
        this.dogCoinContract.methods
          .allowance(account,"0x0fe8400a0923deaa76c81f9188830ab853ff334b")
          .call()
          .then((res) => {
            // var a = web3.utils.fromWei(res, "Gwei");
            if (res > 0) {
              this.isApprove = true;
            }
            console.log("a==========", res);
          })
          .catch((err) => {
            this.$toast.clear();
            this.$toast(err.message);
            console.log(err);
          });
      }
    },
            // 转账授权
    lpApprove2: function () {
      if (this.dogCoinContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      // var address = this.lpInfo.address;
      this.$toast.loading({
        message: "loading",
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      var account = this.account;
      this.dogCoinContract.methods
        .approve('0x7a55a4888979f43bd983c9ab15f64ee58eca49d0', "5000000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("授权=====", res);
          if (res.status) {
            this.$toast.success('OK');
            this.isApprove2 = true;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    // 转账是否授权
    isApproveFun2: function () {
      if (this.dogCoinContract) {
        var account = this.account;
        this.dogCoinContract.methods
          .allowance(account,"0x7a55a4888979f43bd983c9ab15f64ee58eca49d0")
          .call()
          .then((res) => {
            // var a = web3.utils.fromWei(res, "Gwei");
            if (res > 0) {
              this.isApprove2 = true;
            }
            console.log("a==========", res);
          })
          .catch((err) => {
            this.$toast.clear();
            this.$toast(err.message);
            console.log(err);
          });
      }
    },
    transferFun(){
      var len;
    
       if (this.transferContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
        if (!this.allNum) {
        this.$toast(this.$t("message.f5_38"));
        return;
      }
       if (!this.transferList) {
        this.$toast(this.$t("message.f5_39"));
        return;
      }
        if(this.transferList){
        var len = this.transferList.split("\n");
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        message: 'loading',
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(len,this.transferList,_this.allNum*10**9)
      // return
      this.transferContract.methods
        .promo(len, _this.allNum*10**9)
        .send({ from: account })
        .then((res) => {
            _this.$toast.clear();
         _this.$toast.success(_this.$t("message.f5_16"));
         this.ArrList=null;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    investIn(){
      var len;
    
       if (this.invitationContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      //   if (!this.instNum) {
      //   this.$toast(this.$t("message.f5_38"));
      //   return;
      // }
       if (!this.ArrList) {
        this.$toast(this.$t("message.f5_39"));
        return;
      }
        if(this.ArrList){
        var len = this.ArrList.split("\n");
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        message: 'loading',
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(len,this.ArrList)
      // return
      this.invitationContract.methods
        .lockPowderArrOk(len)
        .send({ from: account })
        .then((res) => {
            _this.$toast.clear();
         _this.$toast.success(_this.$t("message.f5_16"));
         this.ArrList=null;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },

  },
  mounted() {
    var lang = localStorage.getItem("lang");
    this.changeLanguage(lang || "en");


    var getData = () => {
        this.add_invitationContract();
        this.add_dogCoinContract();
        this.add_transferContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.invitationContract) {
          getData();
        } else {
          // this.getCoinBalance();
          this.isApproveFun();
          this.isApproveFun2();
          clearInterval(this.timer);
        }
      }, 1000);
    };
    getDataCalculate();

    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 30000);
  },
    destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>

